import "./App.css";
import React, { Suspense, lazy, useEffect } from "react";

import { myStore } from "./store/store";
import { Routes, Route } from "react-router-dom";
import { useCartStatus, useMenuIsOpen } from "./store/store";
import Lenis from "lenis";
import { Loader } from "./components/Loader";

const Home = lazy(() => import("./pages/Home"));
const Lk = lazy(() => import("./pages/Lk"));
const ModalLogin = lazy(() => import("./pages/ModalLogin"));
const ModalRegister = lazy(() => import("./pages/ModalRegister"));
const RecoverForm = lazy(() => import("./pages/RecoverForm"));
const Recover = lazy(() => import("./pages/Recover"));
const Calc = lazy(() => import("./pages/Calc"));

function App() {
  const check = myStore((s) => s.check);
  const cartStatus = useCartStatus();
  const menuStatus = useMenuIsOpen();

  useEffect(() => {
    const lenis = new Lenis({
      lerp: 0.15,
      wheelMultiplier: 1.5,
      prevent: (node) => node.closest('[data-lenis-prevent="true"]'),
    });

    window.__lenis = lenis;

    let rafId;
    const raf = (time) => {
      lenis.raf(time);
      rafId = requestAnimationFrame(raf);
    };
    rafId = requestAnimationFrame(raf);

    return () => {
      cancelAnimationFrame(rafId);
      lenis.destroy();
      window.__lenis = null;
    };
  }, []);

  useEffect(() => {
    check();
  }, [check]);

  useEffect(() => {
    const isScrollLocked = menuStatus || cartStatus;
    let timer;

    const scrollbarWidth =
      window.innerWidth - document.documentElement.clientWidth;

    if (isScrollLocked) {
      document.documentElement.style.overflow = "hidden";

      if (cartStatus) {
        document.body.style.paddingRight = `${scrollbarWidth}px`;
      } else {
        document.body.style.paddingRight = "0px";
      }
    } else {
      timer = setTimeout(() => {
        document.documentElement.style.overflow = "auto";
        document.body.style.paddingRight = "0px";
      }, 50);
    }

    return () => {
      if (timer) {
        clearTimeout(timer);
      }
    };
  }, [menuStatus, cartStatus]);

  return (
    <Suspense fallback={<Loader />}>
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/login" element={<ModalLogin />} />
        <Route path="/profile" element={<Lk />} />
        <Route path="/register" element={<ModalRegister />} />
        <Route path="/RecoverForm" element={<RecoverForm />} />
        <Route path="/recover" element={<Recover />} />
        <Route path="/calc" element={<Calc />} />
      </Routes>
    </Suspense>
  );
}

export default App;
