import { create } from "zustand";
import * as api from "../api/api";
import { jwtDecode } from "jwt-decode";

export const myStore = create((set, get) => ({
  status: "guest",
  user: { id: "", email: "" },

  cartIsOpen: false,
  menuIsOpen: false,

  cartDetails: [],
  purchasesInfo: {},
  purchasesList: [],

  toggleMenu: () => set((state) => ({ menuIsOpen: !state.menuIsOpen })),
  toggleCart: () => set((state) => ({ cartIsOpen: !state.cartIsOpen })),
  setStatus: (newStatus) => set({ status: newStatus }),
  clearCart: () => set({ cartDetails: [] }),
  authCheck: async () => {
    const token = localStorage.getItem("token");
    if (!token) {
      set({ status: "guest", user: null });
      return false;
    }
    const res = await api.fetchApi("/auth/hi", "GET");
    if (!res.ok) {
      localStorage.removeItem("token");
      set({ status: "guest", user: null });
      return false;
    }
    const { email, userID } = jwtDecode(token);
    set({
      status: "auth",
      user: { id: userID, email },
    });
    return true;
  },
  loadCart: async () => {
    let extractedIds = [];
    const cartRes = await api.fetchApi("/auth/basket", "GET");
    if (cartRes.ok && Array.isArray(cartRes.data)) {
      extractedIds = cartRes.data.map((item) => item.ProductID);
    } else {
      set({ cartDetails: [] });
      return [];
    }
    set({ cartDetails: extractedIds });
  },
  loadPurchases: async () => {
    const purchaseRes = await api.fetchApi("/auth/getCourse", "GET");
    if (purchaseRes.ok) {
      const list = Array.isArray(purchaseRes.data) ? purchaseRes.data : [];
      set({ purchasesList: list });
      const purchasesInfo = list.reduce((acc, p) => {
        const id = Number(p.ProductID);
        const days = Number(p?.DifferenceTime?.Days ?? 0);
        const hours = Number(p?.DifferenceTime?.Hours ?? 0);
        const daysLeft = days > 0 ? days + (hours > 0 ? 1 : 0) : hours;
        acc[id] = daysLeft;
        return acc;
      }, {});
      set({ purchasesInfo: purchasesInfo });
      return;
    }
    set({ purchasesInfo: {} });
  },
  check: async () => {
    const ok = await get().authCheck();

    if (!ok) {
      set({
        cartDetails: [],
        purchasesList: [],
        purchasesById: {},
        purchasedIds: [],
      });
      return;
    }
    await Promise.all([get().loadCart(), get().loadPurchases()]);
  },
}));

export const useSetStatus = () => myStore((state) => state.setStatus);
export const useUserEmail = () => myStore((state) => state.user?.email || "");
export const useUserStatus = () => myStore((state) => state.status);
export const useCartDetails = () => myStore((state) => state.cartDetails);
export const useToggleCart = () => myStore((state) => state.toggleCart);
export const useCartStatus = () => myStore((state) => state.cartIsOpen);
export const useClearCart = () => myStore((state) => state.clearCart);
export const useToggleMenu = () => myStore((state) => state.toggleMenu);
export const useMenuIsOpen = () => myStore((state) => state.menuIsOpen);
export const usePurchasesInfo = () => myStore((state) => state.purchasesInfo);
export const usePurchaseList = () => myStore((state) => state.purchasesList);
