const API = process.env.REACT_APP_API_URL;

export const fetchApi = async (endpoint, method, body, params) => {
  try {
    const options = {
      method: `${method}`,
      headers: { "Content-Type": "application/json" },
    };
    if (body) {
      options.body = JSON.stringify(body);
    }

    if (endpoint.startsWith("/auth") || endpoint.startsWith("/hi")) {
      options.headers.Authorization = `Bearer ${localStorage.getItem("token")}`;
    }

    const queryString = new URLSearchParams(params).toString();
    const url = `${API}${endpoint}${queryString ? `?${queryString}` : ""}`;

    const res = await fetch(url, options);

    const json = await res.json();

    if (res.ok) {
      return { ok: true, data: json };
    } else {
      return { ok: false, message: json.err || "  " };
    }
  } catch (err) {
    console.error("fetchApi network error:", err);
    return {
      ok: false,
      message: "     .  .",
      error: err,
    };
  }
};
